import { observer, useLocalStore } from "mobx-react-lite";
import { AccountItem, AppRoute, useApp, useStore } from "../state";
import React, { useEffect } from "react";
import { getTfaCode } from "../Util";
import getSeconds from "date-fns/get_seconds";
import { IconButton, ProgressIndicator } from "office-ui-fabric-react";
import copy from "clipboard-copy";

const timeDelta = 30;

export const Account = observer(function Account(props: {
  account: AccountItem;
}) {
  const store = useStore();
  const appStore = useApp();

  const { account } = props;

  const state = useLocalStore(
    () => ({
      leftTime: 0,
      code: "",
      isValidSecret: true,
      getLeftTime() {
        let nowSecond = getSeconds(store.getNow());
        if (nowSecond > timeDelta) {
          nowSecond -= timeDelta;
        }
        return nowSecond;
      },
      updateCode() {
        state.leftTime = state.getLeftTime();

        try {
          state.code = getTfaCode(props.account.secret, store);
          state.isValidSecret = true;
        } catch (e) {
          state.isValidSecret = false;
          console.log("eeeeee", e);
        }
      }
    }),
    props
  );

  useEffect(() => {
    state.updateCode();
    const timer = window.setInterval(() => {
      state.updateCode();
    }, 1000);
    return () => {
      window.clearInterval(timer);
    };
  }, []);

  const copyCode = () => {
    copy(state.code);
    store.api.notify({ msg: "已复制到剪贴板" });
  };

  const progress = state.leftTime / timeDelta;
  let noTimeToDo = false;
  if (progress > 0.88) {
    noTimeToDo = true;
  }

  return (
    <div className={`Account ${noTimeToDo ? "noTimeToDo" : " "}`}>
      <div className={"Account-panel"}>
        <div className={"code-row"}>
          <div
            className={
              "ms-font-su ms-fontWeight-semibold tfa-code ms-fadeIn100"
            }
            onClick={copyCode}
          >
            {state.isValidSecret ? state.code : "invalid"}
          </div>
          <div className={"action-btns"}>
            <IconButton iconProps={{ iconName: "Copy" }} onClick={copyCode} />
            <IconButton
              iconProps={{ iconName: "PageEdit" }}
              onClick={() => {
                appStore.appRoute = AppRoute.AddOrEdit;
                appStore.addForm.isEdit = true;
                appStore.addForm.secret = account.secret;
                appStore.addForm.remark = account.remark;
              }}
            />

            <IconButton
              iconProps={{ iconName: "PageRemove" }}
              onClick={() => {
                store.removeItem(account.secret);
              }}
            />
          </div>
        </div>
        <div>
          <div className={"ms-font-m"}>
            <pre>{account.remark}</pre>
          </div>
        </div>
        <ProgressIndicator percentComplete={progress} />
      </div>
    </div>
  );
});
