import { useEffect } from "react";
import { TOTP } from "../libs/jsotp/lib/jsotp";
import { CoreStoreClazz } from "./state";

const TOTPs = {};
export const getTfaCode = (secret, store: CoreStoreClazz) => {
  let totp = TOTPs[secret];
  if (!totp) {
    totp = TOTP(secret, 30, store.getNow);
    TOTPs[secret] = totp;
  }
  return totp.now();
};

/**
 //由于 hook 组件，不会有ref ， 所以产生了这个



 */
export const useHookRef = (ref, ins) => {
  useEffect(() => {
    ref && (ref.current = ins.current);
  }, []);
};
