import { observer } from "mobx-react-lite";
import { useStore } from "../state";
import React, { useEffect } from "react";
import { useSetState } from "react-use";
import { addSeconds, format } from "date-fns";
import { Label } from "office-ui-fabric-react";

export const NowTime = observer(function NowTime() {
  const store = useStore();

  const [state, setState] = useSetState({
    systemTime: new Date(),
    usedTime: new Date()
  });

  useEffect(() => {
    const timer = window.setInterval(() => {
      const now = new Date();
      const usedTime = addSeconds(now, store.deltaTime);
      setState({
        systemTime: now,
        usedTime
      });
    }, 1000);

    return () => {
      window.clearInterval(timer);
    };
  });

  return (
    <div className={"NowTime"}>
      <div className={"systemTime"}>
        <Label disabled={true}>
          系统时间:
          {format(state.systemTime, "YYYY-MM-DD HH:mm:ss")}
        </Label>
      </div>
      <div className={"usedTime"}>
        <Label disabled={true}>
          使用时间:
          {format(state.usedTime, "YYYY-MM-DD HH:mm:ss")}
        </Label>
      </div>
    </div>
  );
});
