import { observer } from "mobx-react-lite";
import { AppRoute, useApp, useStore } from "../state";
import React from "react";
import { CommandBar } from "office-ui-fabric-react";

export const AppHeader = observer(function AppHeader() {
  const store = useStore();

  const appStore = useApp();

  const getItems = () => {
    if (appStore.appRoute === AppRoute.Main) {
      return [
        {
          key: "newItem",
          // name: 'New',
          iconProps: {
            iconName: "Add"
          },
          onClick: () => {
            appStore.appRoute = AppRoute.AddOrEdit;
            appStore.addForm.isEdit = false;
            appStore.addForm.secret = "";
            appStore.addForm.remark = "";
          }
        },

        {
          key: "Download",
          iconProps: {
            iconName: "Download"
          },
          disabled: true
        },
        {
          key: "upload",
          iconProps: {
            iconName: "Upload"
          },
          disabled: true
        }
      ];
    } else {
      return [
        {
          key: "NavigateBack",
          iconProps: {
            iconName: "NavigateBack"
          },
          onClick: () => {
            appStore.appRoute = AppRoute.Main;
          }
        }
      ];
    }
  };

  const getFarItems = () => {
    if (appStore.appRoute === AppRoute.Setting) {
      return [
        {
          key: "info",
          name: "Info",
          iconProps: {
            iconName: "Info"
          },
          iconOnly: true,
          onClick: () => console.log("Info")
        }
      ];
    } else {
      return [
        {
          key: "Settings",
          iconProps: {
            iconName: "Settings"
          },
          onClick: () => {
            appStore.appRoute = AppRoute.Setting;
          }
        },

        {
          key: "info",
          name: "TFA-manager",
          iconProps: {
            iconName: "Info"
          },
          iconOnly: true,
          onClick: () => console.log("Info")
        }
      ];
    }
  };

  return (
    <div className={"AppHeader"}>
      <CommandBar items={getItems()} farItems={getFarItems()} />
    </div>
  );
});
