import React, { ReactNode } from "react";
import { CoreStoreClazz } from "./state";

export class ApiCenter {
  constructor(store: CoreStoreClazz) {
    store.api = this;
  }

  notify(notify: { msg: ReactNode; time?: number }) {
    console.log("通知提醒");
  }
}

export const createApi = (store: CoreStoreClazz) => {
  new ApiCenter(store);
};
