import { observer } from "mobx-react-lite";
import { AppRoute, useApp, useStore } from "../state";
import React from "react";
import { PrimaryButton, TextField } from "office-ui-fabric-react";
import { useSetState } from "react-use";

export const AddForm = observer(function AddForm() {
  const store = useStore();

  const appStore = useApp();
  const { secret, remark, isEdit } = appStore.addForm;

  const [state, setState] = useSetState({
    err_secret: null as string,
    err_remark: null as string
  });

  return (
    <div className={"AddForm"}>
      <div>
        <TextField
          label="请输入秘钥"
          required={true}
          disabled={isEdit}
          value={secret}
          onChange={(e: any) => {
            const val = e.target.value.replace(/\s/g, "");
            appStore.addForm.secret = val;
          }}
          errorMessage={state.err_secret}
        />
      </div>

      <div>
        <TextField
          label="请输入其他信息"
          multiline
          rows={3}
          value={remark}
          onChange={(e: any) => {
            appStore.addForm.remark = e.target.value;
          }}
          errorMessage={state.err_remark}
        />
      </div>

      <div className={"addButton"}>
        <PrimaryButton
          text={isEdit ? "更新" : "添加"}
          onClick={() => {
            if (secret.trim() === "") {
              setState({ err_secret: "秘钥不能为空" });
              return 0;
            }
            if (remark.trim() === "") {
              setState({ err_remark: "备注不能为空" });
              return 0;
            }

            if (isEdit) {
              store.updateItem(appStore.addForm);
            } else {
              store.addItem(appStore.addForm);
            }
            appStore.appRoute = AppRoute.Main;
          }}
        />
      </div>
    </div>
  );
});
