import { observer } from "mobx-react-lite";
import { useApp, useStore } from "../state";
import React, { useEffect, useRef } from "react";
import { PrimaryButton } from "office-ui-fabric-react";
import { NowTime } from "./NowTime";
import { useSetState } from "react-use";
import { useHookRef } from "../Util";
import { differenceInSeconds, format, parse } from "date-fns";

const DatePicker = (props: { visible: boolean; hookRef? }) => {
  const [state, setState] = useSetState({
    date: "",
    time: ""
  });

  const dateEl = useRef(null);
  const timeEl = useRef(null);

  const ins = useRef({
    currentDate: null as number
  });

  useEffect(() => {
    const now = new Date();
    setState({
      date: format(now, "YYYY-MM-DD"),
      time: format(now, "HH:mm")
    });
  }, []);

  useEffect(
    () => {
      if (state.date && state.time) {
        ins.current.currentDate = parse(
          state.date + " " + state.time
        ).getTime();
      }
    },
    [state.date, state.time]
  );

  useHookRef(props.hookRef, ins);

  return (
    <div>
      <input
        type={"date"}
        ref={dateEl}
        value={state.date}
        onChange={e => setState({ date: e.target.value })}
      />
      <input
        ref={timeEl}
        type={"time"}
        value={state.time}
        onChange={e => setState({ time: e.target.value })}
      />
    </div>
  );
};

export const SettingForm = observer(function SettingForm() {
  const store = useStore();

  const appStore = useApp();
  const { secret, remark, isEdit } = appStore.addForm;

  const [state, setState] = useSetState({
    timePickerVisible: false
  });

  const datePicker = useRef(null);

  return (
    <div className={"SettingForm"}>
      <NowTime />
      <div className={"sync-btn"}>
        <PrimaryButton
          iconProps={{ iconName: "Sync" }}
          onClick={() => {
            store.saveDeltaTime(0);
            setState({ timePickerVisible: false });
          }}
        >
          使用系统时间
        </PrimaryButton>
      </div>

      <div className={"user-sync-btn"}>
        <PrimaryButton
          iconProps={{ iconName: "Timer" }}
          onClick={() => {
            setState({ timePickerVisible: !state.timePickerVisible });
          }}
        >
          手动设置时间
        </PrimaryButton>
        <div className={"time-set-div"}>
          {state.timePickerVisible && (
            <>
              <DatePicker
                hookRef={datePicker}
                visible={state.timePickerVisible}
              />
              <PrimaryButton
                iconProps={{ iconName: "SkypeCheck" }}
                onClick={() => {
                  console.log(datePicker.current);
                  if (datePicker.current.currentDate) {
                    const now = new Date();
                    const deltaSecond = differenceInSeconds(
                      datePicker.current.currentDate,
                      now
                    );
                    store.saveDeltaTime(deltaSecond);
                  }
                  setState({ timePickerVisible: false });
                }}
              >
                确定使用此时间作为当前时间
              </PrimaryButton>
            </>
          )}
        </div>
      </div>
    </div>
  );
});
