import React, { useEffect } from "react";
import { observer } from "mobx-react-lite";
import {
  AppStore,
  CoreStore,
  createAdapter,
  createAppStore,
  createStore
} from "./state";
import { Main } from "./pages/Main";
import { initializeIcons } from "office-ui-fabric-react";
import * as ls from "store";
import * as _ from "lodash";
import { createApi } from "./ApiCenter";

const lsStoreKey = "tfa_manager";

const lsGet = (key: string, defaultValues: any): any => {
  const lsRootStore = ls.get(lsStoreKey, {});
  const field = lsRootStore[key];
  if (field === undefined) {
    return defaultValues;
  } else {
    return field;
  }
};

const lsSet = (key: string, val: any): any => {
  const lsRootStore = ls.get(lsStoreKey, {});
  lsRootStore[key] = val;
  ls.set(lsStoreKey, lsRootStore);
};

const getAccounts = () => {
  return lsGet("accounts", []);
};

const saveAccounts = accounts => {
  lsSet("accounts", accounts);
};

const ss = createStore(
  createAdapter({
    getInitialList: () => getAccounts(),

    addItem: item => {
      const accounts = getAccounts();
      accounts.push(item);
      saveAccounts(accounts);
    },

    updateItem: item => {
      const accounts = getAccounts();
      const account = _.find(accounts, { secret: item.secret });
      Object.assign(account, item);
      saveAccounts(accounts);
    },

    removeItem: item => {
      const accounts = getAccounts();
      _.remove(accounts, { secret: item.secret });
      saveAccounts(accounts);
    },

    getInitialDeltaTime: () => {
      return lsGet("deltaTime", 0);
    },
    saveDeltaTime: deltaTime => {
      lsSet("deltaTime", deltaTime);
    }
  })
);

// ss.addItem({secret: 'LHGKZFPXUS5ZJONQ', remark: 'aaaaaaaaaaaaaa'})
// ss.addItem({secret: 'LHGKZFPXUS5ZJONK', remark: 'aaaaaaaabbbbb'})

// for (let i = 0; i < 0; i++) {
//   const secret = `LHGKZFPXUS5Z${i}`
//   ss.addItem({secret, remark: secret})
// }

const appss = createAppStore(ss);

createApi(ss);

initializeIcons();

export const App = observer(() => {
  useEffect(() => {}, []);

  return (
    <CoreStore.Provider value={ss}>
      <AppStore.Provider value={appss}>
        <div className={"app"}>
          <div className={"miniApp"}>
            <Main />
          </div>
        </div>
      </AppStore.Provider>
    </CoreStore.Provider>
  );
});
